.wpp-pix-checkout {
  margin-top: 20px;
}

.wpp-pix-checkout > div .payment-message {
  justify-content: center;
  align-items: center;
  display: flex;
}

.wpp-pix-checkout > div .payment-message span {
  color: #6d6d6d;
  font-size: 1.2em;
  font-weight: 600;
}

.wpp-pix-checkout > div .payment-message span i {
  margin-left: 10px;
}

.wpp-pix-checkout .qrcode-image {
  max-width: 100%;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
  max-height: 100% !important;
  height: -moz-fit-content !important;
  height: fit-content !important;
}

.wpp-pix-checkout .qrcode-image img {
  opacity: .6;
  max-width: 150px !important;
  max-height: none !important;
}

